.main {
  padding: 20px;
}

.sidebar-header {
  margin: 25px;
  margin-left: 15px;
}

.sidebar-header h1 {
  color: white;
  font-weight: bold;
}
.sidebar {
  background-color: #005555;
  border-radius: 5px;
  box-shadow: 0 0 2px gray;
  margin-right: 20px;
  height: 100vh;
  padding: 10px;
  width: 300px;
  align-items: center;
  /* margin-left: 0; */
}
.collapsed-sidebar {
  background-color: #005555;
  border-radius: 5px;
  box-shadow: 0 0 2px gray;
  margin-right: 20px;
  height: 100vh;
  padding: 10px;
  width: 100px;
}
.content {
  width: 100%;
  height: 100%;
}
.header {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 2px gray;
  /* padding:10px; */
  margin-bottom: 20px;
  height: 15vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.body {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 2px gray;
  height: 82vh;
  width: 100%;
  padding: 15px;
}
.icons {
  width: px;
}

.menu {
  margin-top: 50px;
  padding: 0 10px;
}
.menu-item {
  margin: 35px;
  margin-left: 20px;
  align-items: center;
}
.menu-item a {
  color: white;
  text-decoration: none;
  font-size: 20px;
  padding: 0 10px;
}
.menu-item i {
  color: white;
  text-decoration: none;
  font-size: 20px;
  /* margin:0 15px; */
  /* margin-left: px; */
}
.active-menu-item {
  color: white;
  background-color: #013535;
  padding: 5px;
  border-radius: 5px;
}
.header-action-icon {
  font-size: 30px;
  color: rgb(0, 0, 0);
  padding-left: 5px;
  cursor: pointer;
}
.header-content {
  display: flex;
  align-items: center;
  height: 50px;
  justify-content: flex-end;
  margin: 0 15px;
}
