.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}
.header-content a {
  text-decoration: none;
  font-size: 1.2rem;
  color: blue;
  margin: 0 10px;
  text-transform: uppercase;
}

/* .ant-scroll-number-only */
.ant-badge-count {
  padding-right: -10px !important;
  padding-bottom: -10px !important;
}
