@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

* {
  font-family: "Montserrat", sans-serif;
}

/* antd override */
input {
  border-radius: 2px !important;
  border: 1px solid gray !important;
  height: 40px !important;
  /* margin-top: 40px; */
}
input:focus,
.ant-picker {
  outline: none;
}

label {
  font-size: 16px !important;
  color: black;
}
.ant-form-item {
  margin: 10px 0 !important;
}
.ant-picker-input input {
  border: none !important;
}
.ant-picker {
  border-radius: 2px !important;
  border: 1px solid gray !important;
  height: 40px !important;
}

.ant-badge-count {
  padding-right: -10px !important;
  padding-bottom: -10px !important;
}

/* .ant-picker-input input
{
  border:none;
} */

/* commom styling  */

.spinner-parent {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  /* opacity: 0.8; */
  background-color: rgba(0, 0, 0, 0.723);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
.spinner-border {
  width: 5rem;
  height: 5rem;
}
.card-title-1 {
  font-size: 22px;
  font-weight: bold;
  color: rgba(0, 0, 0, 627);
}
.normal-text {
  font-size: 1rem;
}
.card {
  box-shadow: 0 0 2px gray;
  border-radius: 0 !important;
}
.button-1 {
  margin-top: 35px;
}

.primary-button {
  background-color: #005555 !important;
  border-color: #005555 !important;
  height: 40px !important;
  width: 100% !important;
  color: white !important;
  font-size: 16px !important;
  width: max-content !important;
}
.anchor {
  color: black !important;
  text-decoration: underline;
  font-size: 16px !important;
}
.anchor:hover {
  text-decoration: underline;
}
/* authentication pages */
.authentication {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #005555;
}
.authentication-form {
  width: 400px;
}
.page-title {
  font-size: 25px;
  color: rgba(0, 0, 0, 0.773);
  font-weight: bold;
}
.card-title {
  font-size: 22px;
  font-weight: bold;
  background-color: orangered;
  color: white !important;
  max-width: max-content !important;
  padding: 7px 15px;
  margin-left: -40px;
  border-bottom-left-radius: 10px;
}
